<template>
  <div id="review-panel">
    <h1 class="list-title">
      <span class="tit-text">{{ title }}</span>
    </h1>
    <Row class="card-box">
      <Col v-for="item in CountData" class="card-col" :span="6" :key="item.name">
        <div class="sim-card" :class="{cur:item.cur}" @click="CountList(item.status)">
          <Icon class="icon" :type="item.icon"></Icon>
          <p class="title">{{ item.name }}</p>
          <p class="value"><span class="num">{{item.count}}</span><span>{{item.cunit}}</span><span class="num" v-show="item.second">/</span><span class="num">{{item.other}}</span><span>{{item.ounit}}</span>
          </p>
          <span class="tips">点击查看</span>
        </div>
      </Col>
    </Row>
    <div class="screen-area">
      <Card>
        <div class="card-tit" slot="title">
          <h3>
            <Icon type="ios-pricetags-outline"></Icon>
            筛选查询
          </h3>
          <div class="btn-box">
            <Button type="ghost" icon="reply" @click="ResetScreen">重置筛选</Button>
            <Button type="success" icon="search" @click="SimpleSearch">查询结果</Button>
          </div>
        </div>
        <div class="opt-box">
          <Form :model="ScreenData" inline :label-width="85">
            <FormItem label="姓名：">
              <Input v-model="ScreenData.name" style="width: 120px"></Input>
            </FormItem>
            <FormItem label="手机号：">
              <Input v-model="ScreenData.phone" style="width: 120px"></Input>
            </FormItem>
          </Form>
        </div>
      </Card>
    </div>
    <div class="data-area">
      <Card :padding="0">
        <div class="card-tit" slot="title">
          <h3>
            <Icon type="clipboard"></Icon>
            用户列表
          </h3>
          <div class="btn-box">
            <Button type="primary" icon="pin" @click="ShowHang">显示挂起</Button>
            <Button type="warning" icon="checkmark" @click="SumPass">一键通过</Button>
          </div>
        </div>
        <Table :columns="UserCol"
               :data="UserData"
               :loading="loading"
               @on-selection-change="SelectTable"></Table>
        <div class="page-box">
          <Page :current="Page.cur"
                :page-size="Page.size"
                :total="Page.count"
                placement="top"
                @on-change="ChangePage"
                @on-page-size-change="ChangeSize" show-sizer></Page>
        </div>
      </Card>
    </div>
    <AuditModal :modalShow="Audit.modal"
                :InitId="Audit.id"
                :UniqueId="Audit.id"
                :AllId="Audit.allId"
                @CloseModal="AuditCancel"></AuditModal>
  </div>
</template>

<script>
  import { getLocal } from '@/util/util'
  import AuditModal from '@/components/infoModal/AuditModal'

  export default {
    name: 'ReviewPanel',
    components: {
      AuditModal
    },
    data () {
      return {
        title: '审核面板',
        apiUrl: '/backend/PromoteInfo/Index',
        auth_id: '',
        loading: true,
        allTime: [],
        //统计数据
        CountData: [{
          name: '审核中',
          icon: 'ios-list-outline',
          count: 0,
          cunit: '人',
          second: false,
          status: 'DAYREG',
          cur: true
        },{
          name: '今日通过（未放款）',
          icon: 'android-calendar',
          count: 0,
          cunit: '人',
          second: false,
          status: 'DAYVIEW',
          cur: false
        },{
          name: '已通过未放款',
          icon: 'android-bookmark',
          count: 0,
          cunit: '人',
          second: false,
          status: 'MONTHREG',
          cur: false
        },{
          name: '不通过列表',
          icon: 'android-delete',
          count: 0,
          cunit: '人',
          second: false,
          status: 'MONTHVIEW',
          cur: false
        }],
        //基础筛选数据
        ScreenData: {
          status: 'DAYREG',
          name: '',
          phone: ''
        },
        UserCol: [
          {
            type: 'selection',
            width: 55,
            align: 'center'
          },{
            title: '客户ID',
            width: '80',
            align: 'center',
            key: 'id'
          },{
            title: '姓名',
            width: '100',
            align: 'center',
            key: 'name'
          },{
            title: '性别',
            key: 'sex'
          },{
            title: '手机',
            key: 'phone'
          },{
            title: '身份证',
            key: 'idcard'
          },{
            title: '系统建议',
            key: 'advise'
          },{
            title: '状态',
            key: 'status'
          },{
            title: '授权状态',
            key: 'auth_status'
          },{
            title: '操作',
            key: 'operation',
            align: 'center',
            width: '220',
            render: (h, params)=>{
              return h('div',this.$renderBtn(h, params, this.BtnData));
            }
          }
        ],
        UserData: [],     //表格数据
        BtnData: [],      //按钮数据
        //群选打钩后操作
        SelectData: [],
        //初始分页信息
        Page: {
          count: 0,
          cur: 1,
          size: 20,
        },
        Audit:{
          modal: false,
          id: '',
          allId: ''
        }
      }
    },
    created(){
      this.auth_id = getLocal('auth_id');
      this.InitData(this.apiUrl);
    },
    computed: {

    },
    methods: {
      //去除data数据里绑定的监视器
      RemoveObserve(rowdata){
        return JSON.parse(JSON.stringify(rowdata));
      },
      //筛选操作(重置筛选)
      ResetScreen(){
        for(let key in this.ScreenData){
          this.ScreenData[key] = '';
        }
        this.allTime = '';
      },
      //多选打钩绑定数据
      SelectTable(data){
        let idarr = [];
        if(data.length > 0){
          data.forEach(val=>{
            idarr.push(val.id);
          })
        }
        this.SelectData = idarr;
      },
      //选择时间
      PickDate(time){
        this.allTime = time;
      },
      //二次获取数据
      SecondData(sinfo){
        return new Promise(resolve=>{
          this.$post(this.apiUrl,sinfo).then(d=>{
            this.UserCol = d.data.field;
            this.UserData = d.data.list;
            this.Page.count = d.data.count;
            this.loading = false;
            resolve();
          })
        });
      },
      //统计列表
      CountList(status){
        this.loading = true;
        let sinfo = this.RemoveObserve(this.ScreenData);
        this.CountData.forEach(val=>{
          if(val.status === status){
            val.cur = true;
          }else{
            val.cur = false;
          }
        });
        this.ScreenData.status = status;
        sinfo.status = status;
        this.SecondData(sinfo);
      },
      //查询结果
      SimpleSearch(sign = 1){
        let sinfo = this.RemoveObserve(this.ScreenData);
        if(this.allTime[0] !== ""){
          sinfo.start_time = this.allTime[0];
          sinfo.end_time = this.allTime[1];
        }else{
          sinfo.start_time = '';
          sinfo.end_time = '';
        }
        this.SecondData(sinfo).then(()=>{
          this.$Message.success('筛选成功！');
        });
      },
      //初始化数据
      InitData(url,params = {}){
        const that = this;
        this.loading = true;
        this.$fetch('/backend/PromoteInfo/topIndex').then(d=>{
          this.CountData.forEach(val=>{
            if(val.second){
              val.count = d.data[val.status].count;
              val.other = d.data[val.status].allamount;
            }else{
              val.count = d.data[val.status];
            }
          })
        });
        //获取按钮信息
        this.$fetch('/backend/Menuauth/listAuthGet',{auth_id: this.auth_id}).then((d)=>{
          this.BtnData = d.data.operation;
        });
        //列表数据获取
        return new Promise((resolve)=>{
          this.$post(url,params).then((d)=>{
            let res = d.data;
            this.Page.count = d.data.count;
            this.UserData = res;
            that.loading = false;
            resolve();
          })
        })
      },
      //提交信息操作
      UploadData(url,info){
        return new Promise((resolve)=>{
          this.$post(url,info).then((d)=>{
            if(d.status === 1){
              this.$Message.success(d.message);
              resolve(d.data);
            }else{
              this.$Message.error(d.message);
            }
          }).catch((err)=>{
            this.$Message.error('服务器繁忙，请稍后再试！');
          })
        })

      },
      //改变页数
      ChangePage(curpage){
        let sinfo = Object.assign(this.ScreenData,{
          page: curpage,
          num: this.Page.size
        });
        this.InitData(this.apiUrl,sinfo).then(()=>{
          this.Page.cur = curpage;
        })
      },
      //改变显示条数
      ChangeSize(size){
        let sinfo = Object.assign(this.ScreenData,{
          page: 1,
          num: size
        });
        this.InitData(this.apiUrl,sinfo).then(()=>{
          this.Page.cur = 1;
          this.Page.size = size;
        })
      },
      AuditPanel(row){
        this.Audit.modal = true;
        this.Audit.id = row.id;
        let idArr = [];
        this.UserData.forEach(val=>{
          idArr.push(val.id);
        });
        this.Audit.allId = idArr;
      },
      AuditCancel(){
        this.Audit.modal = false;
      },
      ShowHang(){

      },
      SumPass(){
        if(this.SelectData.length > 0){
          const id = this.SelectData.join(',');
          this.UploadData('',{id: id}).then(()=>{
            this.InitData(this.apiUrl);
          })
        }else{
          this.$Message.error('请先勾选需要通过的用户！');
        }
      }
    }
  }
</script>


<style lang="less" scoped>
  .list-title{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    font-weight: normal;
    padding-bottom: 10px;
  }
  .data-area{
    padding-top: 20px;
    .ivu-table-wrapper{
      border: none;
    }
    .page-box{
      padding: 20px;
      display: flex;
      flex-direction: row-reverse;
    }
  }
  .card-tit{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .card-box{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
    padding-bottom: 20px;
    .card-col{
      padding: 0 8px;
    }
    .sim-card{
      position: relative;
      width: 100%;
      padding: 15px;
      color: #FFF;
      overflow: hidden;
      cursor: pointer;
      border-radius: 5px;
      background-color: #c3c3c3;
      margin-bottom: 10px;
      .title{
        font-size: 16px;
      }
      .value{
        padding-top: 10px;
        font-size: 18px;
        .num{
          font-size: 32px;
        }
      }
      .tips{
        position: absolute;
        transition: all 0.1s linear;
        bottom: -20px;
        right: 20px;
        font-size: 14px;
      }
      &:hover{
        .tips{
          bottom: 10px;
        }
      }
      .icon{
        position: absolute;
        right: 10px;
        top: 5px;
        font-size: 60px;
      }
      &.cur{
        background-color: #2db7f5;
        &:hover{
          .tips{
            bottom: -20px;
          }
        }
      }
    }
  }
  .count-span{
    font-size: 18px;
    padding: 0 5px;
    color: #d7000f;
  }
</style>



